.music-control {
    margin: 20px;
    font-size: 1.5em;
}

.music-control .music-label {
    margin-right: 20px;
}

.music-switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
}

.music-switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
.slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
}

.music-switch input:checked + .slider {
    background-color: #1a599d;
}

.music-switch input:focus + .slider {
    box-shadow: 0 0 1px #1a599d;
}

.music-switch input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
}
